import CubePage from '../../components/cube-page.vue'
import refumdReaspm from './components/reason.vue'
export default {
  name: 'refundApply',
  data (){
    return {
      placeholder: '退换货类型',
      options: ['退款', '换货'],
      value: '',
      selected: false,
      maskShow: false,
      scrollEvents: ['scroll'],
      scrollY: 0,
      mark: '',//备注
      attr_val: '',//退换货属性
      name: '',
      tell: '',
      type: '',//退换货类型值
      reason:'',//退换货原因
      reasonTs: '请选择退换货原因'
    }
  },
  components: {
    CubePage,
    refumdReaspm
  },
  methods: {
    changeType(type){
      if(type=='退款'){
        this.type = 0
      }else{
        this.type = 1
      }
    },
    showToastTxtOnly(text) {
      this.toast = this.$createToast({
        txt: text,
        type: 'txt'
      })
      this.toast.show()
    },
    showToastType(txt) {
      let that = this
      const toast = this.$createToast({
        txt: txt,
        type: 'correct',
        time: 1000,
        onTimeout: () => {
          that.$router.push({path:'/refund/index'})
        }
      })
      toast.show()
    },
    subClick(){
      let id = this.$route.query.id
      if(!this.value){
        this.showToastTxtOnly('请选择退换货类型')
        return false
      }
      if(!this.reason){
        this.showToastTxtOnly('请选择退换货原因')
        return false
      }
      if(!this.attr_val){
        this.showToastTxtOnly('请填写退换货属性')
        return false
      }
      if(!this.name){
        this.showToastTxtOnly('请填写姓名')
        return false
      }
      if(!this.tell){
        this.showToastTxtOnly('请填写联系方式')
        return false
      }
      if(!this.mark){
        this.showToastTxtOnly('请填写备注信息')
        return false
      }
      let params = {}
      params.id = id
      params.return_type = this.type //退换货类型 0退货 1换货
      params.tell = this.tell // 联系方式
      params.attr_val = this.attr_val //换货属性
      params.cause = this.reason  //退换货原因
      params.remark = this.remark //备注
      this.http.post(this.ports.refund.aftersave,params, res =>{
        this.$store.commit('changeLoading',false)
        console.log(res)
        if(res.success){
          let data = res.data
          this.count = data.count
          this.list = data.res
          this.showToastType(res.msg)
        }else{
          this.showToastTxtOnly(res.msg)
        }
      })

    },
    confirmClick(val){
      const that = this
      this.maskShow = true
      setTimeout( ()=>{
        that.selected = true
      },200)
    },
    changeSelected(val){
      this.selected = val
      const that = this
      if(!this.selected){
        setTimeout( ()=>{
          that.maskShow = false
        },300)
      }
    },
    changeDeleteOrder(val){
      this.reason = val.value
      this.reasonTs = val.label
      this.changeSelected(false)
    },
  },
  mounted (){
    this.$store.commit('changeLoading',false)
  }
}



