export default {
  name: 'mb',
  data (){
    return {
      selected2: 5,
      options2: [
        {
          label: '商品无货',
          value: 1
        },
        {
          label: '发货时问题',
          value: 2
        },
        {
          label: '商品降价了',
          value: 3,
        },
        {
          label: '不想要了',
          value: 4,
        },
        {
          label: '其他原因',
          value: 5,
        }
      ]
    }
  },
  methods: {
    //取消订单
    closeSelected(){
      this.$emit('changeSelected',false)
    },
    handleClickCancel(){
      let data = '';
      for(let i=0;i<this.options2.length;i++){
        if(this.selected2==this.options2[i].value){
          data = this.options2[i]
          this.$emit('changeDeleteOrder',data)
        }
      }
    }
  },
  mounted (){

  }
}



